import Level from './level'
import Player from './player'
import MovingWall from './movingwall'
import Solid from './solid'
import Vec2 from './vec2'
import { CONSTANTS, clearDraw, clamp, debug } from './utils'
import { init as initMouse, mouse, draw as drawMouse } from './mouse'
import levelData from './maps/large_level.json'
import level from './level'
import Stats from 'stats.js'

const levelLoader = document.getElementById('level')
const loadButton = document.getElementById('load')
const stats = new Stats()
stats.showPanel(0)
document.body.appendChild(stats.dom)

const canvas: HTMLCanvasElement = document.getElementById('canvas')
canvas.width = CONSTANTS.WIDTH
canvas.height = CONSTANTS.HEIGHT
const context = canvas.getContext('2d')
context.translate(0.5, 0.5)
context.imageSmoothingEnabled = false

loadButton.addEventListener('click', () => {
    const data = JSON.parse(levelLoader.value)
    Level.clear()
    Level.load(context, data)
})

const player = new Player()
level.addPlayer(player)
Level.load(context, levelData)

let start

const init = function () {
    start = performance.now()
    initMouse(canvas)
}

const update = function (dt: number) {
    for (const actor of Level.actors) {
        actor.update(dt)
    }

    for (const solid of Level.solids) {
        solid.update(dt)
    }

    level.update(dt)
}

const xBuf = CONSTANTS.WIDTH / 2

const render = function () {
    clearDraw(context)
    level.render(context)
    // for (const actor of Level.actors) {
    // actor.drawAABB(context, "#eb34de");
    // }
    drawMouse(context)
}

const tick = function (now: DOMHighResTimeStamp) {
    stats.begin()
    let dt = (now - start) / 1000
    start = now

    update(dt)
    render()
    stats.end()
    requestAnimationFrame(tick)
}

init()
requestAnimationFrame(tick)
