class Tile {
  constructor(sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight) {
    this.sx = sx;
    this.sy = sy;
    this.sWidth = sWidth;
    this.sHeight = sHeight;
    this.dx = dx;
    this.dy = dy;
    this.dWidth = dWidth;
    this.dHeight = dHeight;
  }

  draw(context, tileset) {
    context.drawImage(
      tileset,
      this.sx,
      this.sy,
      this.sWidth,
      this.sHeight,
      this.dx,
      this.dy,
      this.dWidth,
      this.dHeight
    );
  }
}

export default Tile;
