import Actor from './actor'
import Solid from './solid'
import Tile from './tile'
import Player from './player'
import Vec2 from './vec2'
import MovingWall from './movingwall'
import Spike from './spike'
import { clamp, CONSTANTS } from './utils'
import WaterfallBottom from './waterfall_bottom'

interface TileLayers {
    front?: Tile[]
    middle?: Tile[]
    back?: Tile[]
}

const xBuf = CONSTANTS.WIDTH / 2

class Level {
    public player: Player
    public actors: Array<Actor>
    public solids: Array<Solid>
    public decals: any[]
    public tileLayers: TileLayers
    public loaded: boolean = false
    public levelWidth: number = 320
    public levelHeight: number = 180
    private grid: number[][]
    private tileset: HTMLImageElement = new Image()

    constructor() {
        this.player = null
        this.actors = []
        this.solids = []
        this.decals = []
        this.tileLayers = {}

        this.tileset.src = './maps/tileset.png'
        this.tileset.addEventListener('load', () => {
            this.loaded = true
        })
    }

    clear() {
        this.solids = []
        this.decals = []
        this.tileLayers = {}
        this.createBounds()
    }

    createBounds() {
        // Bounds
        this.addSolid(new Solid(new Vec2(this.levelWidth / 2, 0), new Vec2(this.levelWidth / 2, 0))) // TOP
        this.addSolid(
            new Solid(
                new Vec2(this.levelWidth - 1, this.levelHeight / 2),
                new Vec2(0, this.levelHeight / 2)
            )
        ) // RIGHT
        this.addSolid(
            new Solid(
                new Vec2(this.levelWidth / 2, this.levelHeight - 1),
                new Vec2(this.levelWidth / 2, 0)
            )
        ) // BOTTOM
        this.addSolid(
            new Solid(new Vec2(0, this.levelHeight / 2), new Vec2(0, this.levelHeight / 2))
        ) // LEFT
    }

    createTiles(layer, tiles) {
        const { gridCellWidth, gridCellHeight, data, gridCellsX, gridCellsY } = tiles

        const tileLayer = []

        for (let row = 0; row < gridCellsY - 1; row++) {
            for (let col = 0; col < gridCellsX; col++) {
                let tile = data[gridCellsX * row + col]

                if (tile === -1) {
                    continue
                }

                const sx = tile % 16
                const sy = Math.floor(tile / 16)

                const half = new Vec2(8 / 2, 8 / 2)
                const pos = new Vec2(col * 8 + half.x, row * 8 + half.y)

                tileLayer.push(
                    new Tile(sx * 8, sy * 8, 8, 8, pos.x - half.x + 0.5, pos.y - half.y + 0.5, 8, 8)
                )
            }
        }

        this.tileLayers[layer] = tileLayer
    }

    createEntities(entities) {
        const { gridCellWidth, gridCellHeight } = entities

        for (const entity of entities.entities) {
            const { x, y, width, height, values, name } = entity

            const half = new Vec2(width / 2, height / 2)
            const pos = new Vec2(x + half.x, y + half.y)

            switch (name) {
                case 'blocking_wall':
                    this.solids.push(new Solid(pos, half))
                    break
                case 'moving_platform':
                    const { distance, direction, speed } = values
                    this.solids.push(
                        new MovingWall(pos, half, new Vec2(distance, 0), speed, direction)
                    )
                    break
                case 'spikes':
                    this.solids.push(new Spike(pos, half))
                    break

                case 'player':
                    this.player.position.x = x
                    this.player.position.y = y
                    break

                case 'waterfall_bottom':
                    this.decals.push(new WaterfallBottom(new Vec2(x + 8, y + 24)))
                    break
                default:
                    break
            }
        }
    }

    draw(context) {
        if (!this.loaded) {
            return
        }

        for (const tile of this.tileLayers.back) {
            tile.draw(context, this.tileset)
        }

        for (const tile of this.tileLayers.middle) {
            tile.draw(context, this.tileset)
        }

        this.player.render(context)

        for (const tile of this.tileLayers.front) {
            tile.draw(context, this.tileset)
        }

        for (const decal of this.decals) {
            decal.render(context)
        }
    }

    load(context, level) {
        console.log(level)
        this.levelWidth = level.width
        this.levelHeight = level.height
        this.createBounds()
        this.createEntities(level.layers.find((layer) => layer.name === 'entities'))
        this.createTiles(
            'back',
            level.layers.find((layer) => layer.name === 'back')
        )
        this.createTiles(
            'middle',
            level.layers.find((layer) => layer.name === 'middle')
        )
        this.createTiles(
            'front',
            level.layers.find((layer) => layer.name === 'front')
        )
    }

    addPlayer(player: Player) {
        this.actors.push(player)
        this.player = player
    }

    addActor(actor: Actor) {
        this.actors.push(actor)
    }

    addSolid(solid: Solid) {
        this.solids.push(solid)
    }

    render(context: CanvasRenderingContext2D) {
        const camPosX = -clamp(
            this.player.position.x - this.player.half.x - xBuf,
            0,
            this.levelWidth - CONSTANTS.WIDTH
        )
        context.translate(camPosX + 0.5, 0.5)

        for (const solid of this.solids) {
            if (solid instanceof MovingWall) {
                solid.drawAABB(context, '#ff0000')
            }
        }

        this.player.render(context)
        this.draw(context)
    }

    update(dt) {
        for (const decal of this.decals) {
            decal.update(dt)
        }
    }
}

export default new Level()
